import React from 'react';
import { ResourceInput } from './resourceInput';
// This wrapper provides text input value as the search prop for ResourceInput.
// This is used to create a query param to retrieve cached API requests.
export class ResourceTypeahead extends React.Component {
    constructor(props) {
        super(props);
        this.defaultState = {
            search: undefined,
        };
        this.state = Object.assign({}, this.defaultState);
        this.handleOnClear = () => {
            this.setState({
                search: undefined,
            });
        };
        this.handleOnSearch = (value) => {
            this.setState({
                search: value,
            });
        };
        this.handleOnSelect = (value) => {
            const { onSelect } = this.props;
            this.setState({
                search: undefined,
            }, () => {
                if (onSelect) {
                    onSelect(value);
                }
            });
        };
        this.handleOnClear = this.handleOnClear.bind(this);
        this.handleOnSearch = this.handleOnSearch.bind(this);
        this.handleOnSelect = this.handleOnSelect.bind(this);
    }
    render() {
        const { isDisabled, resourcePathsType, resourceType } = this.props;
        const { search } = this.state;
        return (React.createElement(ResourceInput, { isDisabled: isDisabled, onClear: this.handleOnClear, onSearchChanged: this.handleOnSearch, onSelect: this.handleOnSelect, resourcePathsType: resourcePathsType, resourceType: resourceType, search: search }));
    }
}
